import React, {useState} from "react";
import DotComponent from "./DotComponent";
import "../../../css/custom.css"
import useClickOutside from "../shared/useClickOutside";
import {PopupComponent} from "./PopupComponent";
import {Wine} from "../../model/Wine";
import {useWineConcat, WineInList} from "../shared/useWineConcat";
import wineList from "../WineList/data/wine-list.json"
import wines from "../../model/data/wines.json"
import {WineList} from "../WineList";

const VineyardTopComponent: React.FC<{ showDots?: boolean }> = ({showDots = true}) => {

    const [position, setPosition] = useState<{ x: number, y: number }>();

    const [showPopup, setShowPopup] = useState(false);
    const wineInLists = useWineConcat<WineInList>(wines as Wine[], wineList as WineList[]);

    const [selectedWine, setSelectedWine] = useState<WineInList>();

    const {wrapperRef} = useClickOutside(() => closePopup())

    const closePopup = () => {
        setShowPopup(false);
    };

    const handleClick = (e: any, wineId: number) => {
        if (selectedWine?.id === wineId)
        {
            setSelectedWine({...selectedWine, id: 0})
            return
        }
        else
        {
            const newPosition = {x: verifyEdgePositions(e.clientX), y: e.clientY};

            setTimeout(() => {
                setShowPopup(!showPopup);
                setSelectedWine(wineInLists.find(wine => wine.id === wineId));
                setPosition(newPosition);
            }, 300)
        }
    }

    const verifyEdgePositions = (positionX: number) => {
        return checkLeftEdgePosition(checkRightEdgePosition(positionX));
    };

    const checkLeftEdgePosition = (positionX: number) => {
        return positionX - (positionX < 105 ? 0 : 105)
    };

    const checkRightEdgePosition = (positionX: number) => {
        return positionX - (positionX < window.innerWidth - 144 ? 0 : window.innerWidth - positionX)
    };

    return (
        <div
            className={"row row-no-padding vc_custom_1469089433188 row-o-columns-middle" + (window.innerWidth < 768 ? "" : " row-o-full-height")}>
            <div className="wpb_column vc_column_container column-3_12">
                <div className="vc_column-inner ">
                    <div className="wpb_wrapper" style={{display: (showDots ? "block" : "none")}}>
                        <div style={{width: ''}} className="cqtooltip-wrapper hide_on_mobile"><img
                            src="/images/spacer.png" alt=""/>
                            <div className="cq-hotspots">
                                <DotComponent position={{top: 60, left: 20}} onClick={(e) => handleClick(e, 1)}/>
                                <DotComponent position={{top: 80, left: 90}} onClick={(e) => handleClick(e, 5)}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="wpb_column vc_column_container column-6_12">
                <div className="vc_column-inner ">
                    <div className="wpb_wrapper">
                        <div className="vc_empty_space  height_huge" style={{height: '0px'}}/>
                        <div className="vc_empty_space  height_huge" style={{height: '0px'}}/>
                        <div className="wpb_single_image wpb_content_element vc_align_center  vc_custom_1469087371560">
                            <figure className="wpb_wrapper vc_figure">
                                <div className="vc_single_image-wrapper vc_box_border_grey">
                                    {/*<img width={520}*/}
                                    {/*     height={120}*/}
                                    {/*     src={FileUtils.getImage("bugara.png")}*/}
                                    {/*     className="vc_single_image-img attachment-full"*/}
                                    {/*     alt="bugara"/>*/}
                                </div>
                            </figure>
                        </div>
                        <div className="vc_empty_space  height_medium" style={{height: '0px'}}/>
                        <div className="wpb_text_column wpb_content_element  vc_custom_1469087355036">
                            <div className="wpb_wrapper">
                                <p style={{
                                    textAlign: 'center',
                                    fontSize: '1.6em',
                                    letterSpacing: '2px',
                                    lineHeight: '1.75em'
                                }}><strong><span style={{color: '#ffffff'}}>RODZINNA PRODUKCJA NAJWYŻSZEJ JAKOŚCI WIN Z WŁASNYCH UPRAW</span></strong>
                                </p>
                            </div>
                        </div>
                        <div className="vc_empty_space  height_huge" style={{height: '0px'}}/>
                        <div className="vc_empty_space  height_huge" style={{height: '0px'}}/>
                    </div>
                </div>
            </div>
            <div className="wpb_column vc_column_container column-3_12">
                <div className="vc_column-inner ">
                    <div className="wpb_wrapper" style={{display: (showDots ? "block" : "none")}}>
                        <div style={{width: ''}} className="cqtooltip-wrapper hide_on_mobile"><img
                            src="images/spacer.png" alt=""/>
                            <div className="cq-hotspots">
                                <DotComponent position={{top: 35, left: 10}} onClick={(e) => handleClick(e, 6)}/>
                                <DotComponent position={{top: 60, left: 65}} onClick={(e) => handleClick(e, 10)}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <PopupComponent position={position} show={showPopup} wrapperRef={wrapperRef} wine={selectedWine}/>
        </div>
    )
}

export default VineyardTopComponent;